import { useState } from "react";
import { Mic, MicOff, Stethoscope } from "lucide-react";
import { useTranslation } from "react-i18next";

import { Button } from "@/components/ui/button";
import StatusMessage from "@/components/ui/status-message";

import useRealTime from "@/hooks/useRealtime";
import useAudioRecorder from "@/hooks/useAudioRecorder";
import useAudioPlayer from "@/hooks/useAudioPlayer";

import { GroundingFile, ToolResult } from "./types";

// Table that only shows parsed values (never raw JSON)
function DataTable({ data }: { data: Array<Record<string, any>> }) {
  if (!data.length) {
    return <div className="min-h-[120px]" />; // Empty space for layout consistency
  }

  // Collect all unique columns
  const columns = Array.from(
    data.reduce((set: Set<string>, row) => {
      Object.keys(row).forEach(key => set.add(key));
      return set;
    }, new Set<string>())
  );

  return (
    <div className="w-full max-w-2xl bg-white rounded-xl shadow-xl overflow-x-auto">
      <table className="w-full">
        <thead className="bg-blue-50">
          <tr>
            {columns.map(col => (
              <th key={col} className="px-6 py-4 text-left text-sm font-semibold text-blue-700 uppercase tracking-wider">
                {col.replace(/_/g, " ")}
              </th>
            ))}
          </tr>
        </thead>
        <tbody className="divide-y divide-blue-100">
          {data.map((row, idx) => (
            <tr key={idx} className="hover:bg-blue-50 transition-colors">
              {columns.map(col => (
                <td key={col} className="px-6 py-4 whitespace-nowrap text-sm text-gray-800">
                  {row[col] !== undefined && row[col] !== null && row[col] !== ""
                    ? String(row[col])
                    : <span className="text-gray-400">—</span>}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

function App() {
  const [isRecording, setIsRecording] = useState(false);
  const [groundingFiles, setGroundingFiles] = useState<GroundingFile[]>([]);

  // Parse each grounding file's content as JSON, ignore invalid JSONs
  const parsedData = groundingFiles
    .map(file => {
      try {
        const parsed = JSON.parse(file.content);
        return typeof parsed === "object" && !Array.isArray(parsed) && parsed !== null
          ? parsed
          : null;
      } catch {
        return null;
      }
    })
    .filter((row): row is Record<string, any> => !!row);

  const { startSession, addUserAudio, inputAudioBufferClear } = useRealTime({
    onWebSocketOpen: () => {},
    onWebSocketClose: () => {},
    onWebSocketError: () => {},
    onReceivedError: () => {},
    onReceivedResponseAudioDelta: message => {
      isRecording && playAudio(message.delta);
    },
    onReceivedInputAudioBufferSpeechStarted: () => {
      stopAudioPlayer();
    },
    onReceivedExtensionMiddleTierToolResponse: message => {
      const result: ToolResult = JSON.parse(message.tool_result);

      // Add new grounding files to state
      const files: GroundingFile[] = result.sources.map(x => ({
        id: x.chunk_id,
        name: x.title,
        content: x.chunk
      }));

      setGroundingFiles(prev => [...prev, ...files]);
    }
  });

  const { reset: resetAudioPlayer, play: playAudio, stop: stopAudioPlayer } = useAudioPlayer();
  const { start: startAudioRecording, stop: stopAudioRecording } = useAudioRecorder({
    onAudioRecorded: addUserAudio
  });

  const onToggleListening = async () => {
    if (!isRecording) {
      startSession();
      await startAudioRecording();
      resetAudioPlayer();
      setIsRecording(true);
    } else {
      await stopAudioRecording();
      stopAudioPlayer();
      inputAudioBufferClear();
      setIsRecording(false);
    }
  };

  const { t } = useTranslation();

  return (
    <div className="min-h-screen flex flex-col bg-gradient-to-br from-blue-50 via-white to-green-50 text-gray-900 font-sans">
      <header className="flex flex-col items-center justify-center py-10 bg-white/80 shadow-lg">
        <Stethoscope className="h-14 w-14 text-blue-600 mb-3 drop-shadow-md" />
        <h1 className="text-4xl md:text-5xl font-extrabold text-blue-700 tracking-tight mb-2 text-center">
          Voice Enabled Healthcare Price Transparency
        </h1>
        <p className="text-blue-800 text-lg font-medium text-center max-w-2xl">
          Speak, search, and discover clear healthcare pricing—empowering your care decisions.
        </p>
      </header>
      <main className="flex-grow flex flex-col items-center justify-center px-4 py-8">
        <div className="mb-8 flex flex-col items-center">
          <Button
            onClick={onToggleListening}
            className={`h-14 w-72 text-lg font-semibold flex items-center justify-center shadow-lg rounded-full transition-all duration-200 ${
              isRecording
                ? "bg-green-600 hover:bg-green-700"
                : "bg-blue-600 hover:bg-blue-700"
            }`}
            aria-label={isRecording ? t("app.stopRecording") : t("app.startRecording")}
          >
            {isRecording ? (
              <>
                <MicOff className="mr-3 h-6 w-6" />
                {t("app.stopConversation")}
              </>
            ) : (
              <>
                <Mic className="mr-3 h-7 w-7" />
                {t("app.startRecording")}
              </>
            )}
          </Button>
          <StatusMessage isRecording={isRecording} />
        </div>
        <section className="w-full flex flex-col items-center">
          <DataTable data={parsedData} />
        </section>
      </main>
      <footer className="py-6 text-center bg-white/80 border-t border-blue-100 shadow-inner">
        <p className="text-blue-700 font-semibold text-lg">
          Empowering patients with transparent healthcare pricing for informed choices.
        </p>
      </footer>
    </div>
  );
}

export default App;
